/* Registration Card Section
--------------------------------------------------------------------------------------*/
.registared-student {
    height: 50em;
    padding: 2em;
    overflow: auto;

    #header {
        margin: 0 auto;

        h3 {
            text-align: center;
        }
    }
}

.student-card {
    display: grid;
    grid-template-columns: auto;
    grid-template-rows: auto;

    div:nth-child(1) {
        grid-column: 1/2;
        grid-row: 1/2;
        display: flex;
        justify-content: center;

        span:nth-child(1) {
            flex: 0 0 7em;
        }

        span:nth-child(2) {
            flex: 0 0 7em;
        }

        span:nth-child(3) {
            flex: 0 0 7em;
        }

        span:nth-child(4) {
            flex: 0 0 10em;
        }
        
        span:nth-child(5) {
            flex: 0 0 16em;
        }

        span:nth-child(6) {
            flex: 0 0 6em;
        }

        span:nth-child(7) {
            flex: 0 0 4em;
        }

        span:nth-child(8) {
            flex: 0 0 10em;
        }

        span:nth-child(9) {
            flex: 0 0 10em;
        }

        span:nth-child(10) {
            flex: 1 0 auto;
        }
    }

    div:nth-child(2) {
        grid-column: 1/2;
        grid-row: 1/2;

        display: flex;
        justify-content: right;
        align-items: center;
        padding-right: 1em;
        display: none;

        button {
            width: 130px;
            border: none;
            padding-left: 0;
            padding-right: 0;
            font-size: 1em;

            overflow: hidden;

            position: relative;
            z-index: 1;
            background-color: hsla(0, 0%, 100%, 0.5);

            a {
                display: flex;
                justify-content: center;
                color: #fff;
                text-decoration: none;
                text-align: center;
            }
        }

        button::before {
            content: "";
            width: 100%;
            height: 30px;
            position: absolute;
            z-index: -1;
            top: 0;
            left: 0;
            background-color: hsla(0, 0%, 0%, 0.3);
    
            transform: translate3d(0px, -30px, 0px);
            transform-origin: top center;
    
            transition-property: transform;
            transition-duration: 0.3s;
            transition-delay: 0s;
            transition-timing-function: ease-out;
        }
    
        button:hover,
        button:focus {
            color: #fff;
        }
    
        button:hover:before,
        button:focus:before {
            transform: translate3d(0px, 0px, 0px);
        }

        button:nth-of-type(1) {
            margin-right: 10px;
        }
    }

    &:hover {
        div:nth-child(2) {
            display: flex;
            background-color: hsla(0, 0%, 0%, 0.8);
        }
    }
}

div[class^=student-card]:nth-of-type(2n+0) {
    span {
        background-color: hsla(0, 0%, 0%, 0.5);
    }
}

div[class^=student-card]:nth-of-type(2n+1) {
    span {
        background-color: gray;
    }
}

.table-header{
    div:nth-child(1) {
        span {
            border-bottom: 1px solid #000;
            background-color: initial;
        }
    }
}

