/* Footer Section
--------------------------------------------------------------------------------------*/
footer {
    background-color: $footerbg;

    #footer-wrap {
        display: grid;
        grid-template-columns: repeat(4, 5em);
        grid-template-rows: repeat(2, auto);
        justify-content: center;
        grid-row-gap: 1em;

        padding-top: 1em;
    }

    figure {
        width: 50px;
        margin: 0 auto;
    }

    img {
        display: block;
        width: 100%;
        margin: 0;
    }

    figure:nth-of-type(1) {
        grid-column: 1/2;
        grid-row: 1/2;
    }

    figure:nth-of-type(2) {
        grid-column: 2/3;
        grid-row: 1/2;
    }
    
    figure:nth-of-type(3) {
        grid-column: 3/4;
        grid-row: 1/2;
    }

    figure:nth-of-type(4) {
        grid-column: 4/5;
        grid-row: 1/2;
    }

    div {
        grid-column: 1/5;
        grid-row: 2/3;

        p {
            font-size: 1.5em;
            margin: 0;
            text-align: center;
        }
    }

    a {
        display: flex;
    }
}