/* Picture Slide Section
--------------------------------------------------------------------------------------*/
#slid-wrap {
    display: grid;
    grid-template-columns: repeat(1, 100vw);
    grid-template-rows: repeat(1, 350px);

    overflow: hidden;

    div {
        grid-column: 1/1;
        grid-row: 1/1;

        display: flex;
        overflow-x: hidden;
    }

    figure {
        margin: 0;

        display: grid;
        grid-template-columns: 100vw;
        grid-template-rows: 350px;

        transition-property: margin-left;
        transition-duration: 2s;
        transition-delay: 0s;
        transition-timing-function: cubic-bezier(0.5,-0.22, 0.49, 1.38);
    }

    img {
        display: block;
        width: 100%;
        height: 350px;
        margin: 0;

        grid-column: 1/1;
        grid-row: 1/1;
    }

    figcaption {
        width: 400px;
        margin: 0 auto;
        text-align: center;
        opacity: 0.5;
        color: #fff;
        font-size: 1.5em;
        background-color: #000;

        grid-column: 1/1;
        grid-row: 1/1;
        align-self: flex-end;
        margin-bottom: 3em;
    }

    #btn-holders {
        grid-column: 1/1;
        grid-row: 1/1;

        align-self: flex-end;

        width: 200px;
        height: 22px;
        margin: 0 auto;
        padding-top: 5px;
        background-color: hsla(0, 1%, 15%, 0.2);

        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }

    label {
        width: 13px;
        height: 13px;
        margin-left: 10px;
        border: 2px solid #000;
        border-radius: 50%;

        transition-property: background-color;
        transition-duration: 2s;
        transition-delay: 0s;
        transition-timing-function: ease-in-out;
        
    }

    label:hover {
        cursor: pointer;
    }

}