/*  Header Section
--------------------------------------------------------------------------------------*/
.head {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    opacity: 0.8;
    background-color: $headerbg;
}

nav {
    flex: 0.8 0 0;
    order: 2;
    align-self: center;

    button {
        display: none;
    }
}

.nav {
    display: initial;
    
    ul {
        margin: 0;
        padding: 0;
        display: flex;
        flex-wrap: wrap;
    }
    
    li {
        flex: 1 0 0;
        list-style: none;
    }

    a {
        display: flex;
        justify-content: center;
        text-decoration: none;
        color: #fff;
        font-size: 1.5vw;
        position: relative;
    }

    a:before {
        content: "";
        position: absolute;
        width: 0;
        height: 0.2vw;
        bottom: 0;
        background-color: #fff;

        transition-property: width;
        transition-duration: 0.5s;
        transition-timing-function: ease-out;
        transition-delay: 0s;
    }

    a:hover:before {
        width: 100%;
    }
}

header {
    flex: 0 0 100;
    order: 0;
    align-self: center;

    h1 {
        margin: 0;
        padding-left: 1.3em;
        font-size: 1.4vw;
        color: #fff;
    }
}

#user-aria {
    order: 3;
    align-self: center;

    padding-right: 2.5em;

    button {
        display: inline-block;
        width: 100px;
        height: 20px;
        padding: 0;
        border: none;

        font-weight: bold;
        overflow: hidden;

        position: relative;
        z-index: 1;
    }

    button:before {
        content: "";
        width: 100%;
        height: 20px;
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        background-color: hsla(0, 0%, 0%, 0.8);

        transform-origin: center;
        transform: translate3d(0px, -30px, 0px);

        transition-property: transform;
        transition-duration: 0.3s;
        transition-timing-function: ease-out;
        transition-delay: 0s;

    }

    button:hover:before {
        transform: translate3d(0px, 0px, 0px);
    }
    
    button:hover a {
        color: #fff;
    }

    a {
        display: flex;
        justify-content: center;
        padding: 3px;
        text-decoration: none;
        color: #000;
    }
}

#hide-part {
    display: inline-block;
    margin-right: 5px;
}

/*  Media Query Section
--------------------------------------------------------------------------------------*/
@include breakpoints(min-width, 115em) {
    #user-aria {

        button {
            width: 200px;
            height: 50px;

            font-size: 1.5em;
        }

        button:before {
            height: 50px;
            transform: translate3d(0px, -50px, 0px);
        }
    }
};


@include breakpoints(min-width, 130em) {
    #user-aria {

        button {
            width: 200px;
            height: 100px;

            font-size: 3em;
        }

        button:before {
            height: 100px;
            transform: translate3d(0px, -100px, 0px);
        }
    }
};

@include breakpoints(max-width, 30em) {
   #user-aria {
       padding-right: 0.5em;

       button {
            height: auto;
            font-size: 0.7em;
        }

        button:before {
            height: 30px;
            transform: translate3d(0px, -30px, 0px);
        }
   }

   nav {
    order: 1;
    padding-left: 0.5em;

    flex: 0 0 30px;

    button {
        display: block;
        width: 25px;
        border: none;
        border-radius: 10%;
    }

    img {
        display: block;
        width: 100%;
        margin: 0;
    }
   }

   .nav {
    display: none;
    position: relative;

    ul {
        width: 150px;
        flex-direction: column;
        position: absolute;    
        background-color: hsla(0, 0%, 6%, 0.8);
    }

    a {
        justify-content: left;
        font-size: 1em;
    }

   }

   header {
    order: 2;

    h1 {
        font-size: 1em;
    }
   }

   #hide-part {
    display: none;
   }
};
