/*  Student Registration Edit Section
--------------------------------------------------------------------------------------*/
#edit-section {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    left: 0;
    right: 0;
    height: 0;
    z-index: 1;
    opacity: 0;
    background: #fff;
    visibility: hidden;
    transition: 0.6s;

    figure {
        display: none;
    }
}

#edit-section:target {
    height: 250vh;
    opacity: 1;
    visibility: visible;

    form {
        position: relative;
    }

    fieldset {

        hgroup {
        
            h3 {
                text-align: center;
            }
        }
        
        figure {

            img {
                display: block;
                width: 100%;
                margin: 0;
            }
        }

        .input-group {
            position: relative;
            perspective: 300rem;
    
            input {
                border: none;
                outline: none;
                border-bottom: 1px solid #000;
                background-color: transparent;
            }
    
            label {
                position: absolute;
                top: 0;
                left: 0;
                font-size: 0.8em;
    
                transform: translate3d(0px, 0px, 0px);
                transform-origin: top;
    
                transition-property: transform, font-size;
                transition-duration: 0.5s, 0.3s;
                transition-delay: 0s, 0s;
                transition-timing-function: ease-out;
            }
    
            input:focus + label,
            input:valid + label {
                transform: translate3d(0px, -18px, 0px);
                font-size: 0.6em;
            }
        }
    }
}

.close {
    position: absolute;
    right: 1.5rem;
    top: 0.5rem;
    font-size: 2rem;
    font-weight: 900;
    text-decoration: none;
    color: inherit;
}


/* Media Query Section
--------------------------------------------------------------------------------------*/
@include breakpoints(min-width, 18em) {
    #edit-section:target {
        fieldset {
            display: grid;
            grid-template-columns: minmax(16em, 18em);
            grid-template-rows: repeat(14, auto);
            justify-content: center;
            grid-row-gap: 2em;
            background-color: lightgray;
    
            hgroup {
                grid-column: 1/2;
                grid-row: 1/2;
            }
        
            span:nth-of-type(1) {
                grid-column: 1/2;
                grid-row: 2/3;
            }
        
            span:nth-of-type(2) {
                grid-column: 1/2;
                grid-row: 3/4;
            }
        
            span:nth-of-type(3) {
                grid-column: 1/2;
                grid-row: 4/5;
            }
        
            span:nth-of-type(4) {
                grid-column: 1/2;
                grid-row: 5/6;
            }
        
            span:nth-of-type(5) {
                grid-column: 1/2;
                grid-row: 6/7;
            }

            span:nth-of-type(6) {
                grid-column: 1/2;
                grid-row: 7/8;
            }
        
            span:nth-of-type(7) {
                grid-column: 1/2;
                grid-row: 8/9;
            }
        
            span:nth-of-type(8) {
                grid-column: 1/2;
                grid-row: 9/10;
            }
        
            span:nth-of-type(9) {
                grid-column: 1/2;
                grid-row: 10/11;
            }
        
            span:nth-of-type(10) {
                grid-column: 1/2;
                grid-row: 11/12;
            }
            
            figure {
                grid-column: 1/2;
                grid-row: 12/13;

                width: 8em;

                display: grid;
                grid-template-columns: repeat(2, auto);
                grid-template-rows: auto;
                grid-column-gap: 1em;
                justify-content: center;
                
                img:nth-child(1) {
                    grid-column: 1/2;
                    grid-row: 1/2;
                }

                img:nth-child(2) {
                    grid-column: 2/3;
                    grid-row: 1/2;
                }
            }

            span:nth-of-type(11) {
                grid-column: 1/2;
                grid-row: 13/14;

                #hide {
                    display: none;
                }
            }

            span:nth-of-type(12) {
                grid-column: 1/2;
                grid-row: 14/15;
                margin: 0 auto;
    
                button {
                    width: 200px;
                    padding: 5px;
                    border: none;
                    font-size: 1em;
                    background-color: gray;
        
                    position: relative;
                    z-index: 1;
                    perspective: 300rem;
                    overflow: hidden;
                }
        
                button:before {
                    content: "";
                    position: absolute;
                    z-index: -1;
                    width: 100%;
                    height: 27px;
                    top: 0;
                    left: 0;
                    background-color: hsla(0, 0%, 0%, 0.3);
                    
                    transform: translate3d(0px, -30px, 0px);
                    transform-origin: top;
        
                    transition-property: transform;
                    transition-duration: 0.3s;
                    transition-delay: 0s;
                    transition-timing-function: ease-out;
                }
                
                button:hover,
                button:focus {
                    color: #fff;
                }
        
                button:hover:before,
                button:focus:before {
                    transform: translate3d(0px, 0px, 0px);
                }
            }
        }
    }
   
}

@include breakpoints(min-width, 45em) {
    #edit-section:target {
        fieldset {
            grid-template-columns: repeat(2, 20em);
            grid-template-rows: repeat(9, auto);
    
            hgroup {
                grid-column: 1/3;
                grid-row: 1/2;
            }
        
            span:nth-of-type(1) {
                grid-column: 1/2;
                grid-row: 2/3;
            }
        
            span:nth-of-type(2) {
                grid-column: 1/2;
                grid-row: 3/4;
            }
        
            span:nth-of-type(3) {
                grid-column: 1/2;
                grid-row: 4/5;
            }
        
            span:nth-of-type(4) {
                grid-column: 1/2;
                grid-row: 5/6;
            }
        
            span:nth-of-type(5) {
                grid-column: 1/2;
                grid-row: 6/7;
            }

            span:nth-of-type(6) {
                grid-column: 1/2;
                grid-row: 7/8;
            }
        
            span:nth-of-type(7) {
                grid-column: 1/2;
                grid-row: 8/9;
            }
        
            span:nth-of-type(8) {
                grid-column: 2/3;
                grid-row: 8/9;
            }
        
            span:nth-of-type(9) {
                grid-column: 1/2;
                grid-row: 9/10;
            }
        
            span:nth-of-type(10) {
                grid-column: 2/3;
                grid-row: 9/10;
            }
            
            span:nth-of-type(11) {
                grid-column: 1/2;
                grid-row: 10/11;
                align-self: center;
            }

            figure {
                grid-column: 2/3;
                grid-row: 10/11;
                margin: 0;
            }

            span:nth-of-type(12) {
                grid-column: 1/3;
                grid-row: 11/12;
                
                button {
                    display: block;
                    width: 300px;
                }
            }
        }
    }
}